@keyframes TitleSqueeze {
  40% {transform: scale(1.5, 0.8);}
  70% {transform: scale(0.8, 1.2);}
  85% {transform: scale(1.1, 0.9);}
}

@keyframes TitleShrink {
  0% {font-size: 500px;}
}

@keyframes FadeIn {
  0%   { opacity: 0; }
  92%  { opacity: 0; }
  100% { opacity: 1; }
}

a {
  color: #00c0ce;
}

.title {
  position: relative;
  padding: 0.4vw;
  font-size: 4vw;
  font-style: oblique;
  text-align: center;
  display: inline-block;
  transform: rotate(-2deg);
  animation: TitleSqueeze 1s ease;
}

.bordered {
  border: 0.5vw solid;
  border-radius: 1vw;
  padding: 0.4vw;
  width: 3.5vw;
  display: inline-block;
  transform: skew(10deg);

}

.links {
/*background-color: (200,200,200,0.8);*/
  font-weight: bold;
  font-size: calc(4px + 1vw);
  position: absolute;
  top: 2vw;
  right: 0vw;
  padding: 2vw;
  align-items: flex-end;
  animation: 3s FadeIn;
}

.links > * :hover {
  background-color: rgb(255, 255, 255, 0.3);
  color: white;
}

.links :active {
  color: #fff200;
  border-color: #fff200;
  background-color: rgb(0, 0, 0, 0.1);
}

.links a {
  background-color: rgb(0, 0, 0, 0.5);
  text-decoration: none;
  border: calc(0.25px + 0.25vw) solid;
  border-radius: 1vw;
  border-color: #00c0ce;
  padding: 0.6vw;
  display: inline-block;
  transform: skew(10deg);
  font-style: oblique;
}

